/* Tooltip */
.has-tooltip {
  position: relative;
}
.has-tooltip:hover::after {
  -ms-transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  background: #32373c;
  border-radius: 4px;
  bottom: calc(100% + 10px);
  color: #ffffff;
  content: attr(data-tooltip);
  font-size: 11px;
  height: auto;
  left: 50%;
  line-height: normal;
  max-height: 120px;
  overflow: auto;
  padding: 5px 3px;
  position: absolute;
  transform: translateX(-50%);
  white-space: normal;
  width: calc(100% + 30px);
  z-index: 9;
}
.has-tooltip:hover::before {
  -ms-transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #32373c;
  bottom: 100%;
  content: "";
  height: 0;
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
  width: 0;
}

